.friendMessage {
    display: flex;
    max-width: 1280px ;
    flex-direction: column;
    margin: 0 auto;
}

.friendImageAndText {
    display: flex;
    gap: 10px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 100px;

}

.friendText {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */
    color: #000000;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px;
}

.friendText:after {

}

.friendName {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #000000;

}

.friendMessageText {

}

.friendTime {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    margin-left: 10px;
}
